/**
 * IPlayerOptions
 * @description - The data contract interface used for storing references to player options.
 */
export interface IPlayerOptions {
    autoplay?: boolean;
    mute?: boolean;
    loop?: boolean;
    playPauseTrigger?: boolean;
    controls?: boolean;
    maskLevel?: number;
    market?: string;
    playbackSpeed?: boolean;
    download?: boolean;
    playFullScreen?: boolean;
    hidePosterFrame?: boolean;
    autoCaptions?: string;
    flexSize?: boolean;
    aspectRatio?: number;
    userMinimumAge?: number;
    showEndImage?: boolean;
    showImageForVideoError?: boolean;
    inviewPlay?: boolean;
    inviewThreshold?: number;
    inViewWidthFraction?: number;
    inViewHeightFraction?: number;
    controlPanelTimeout?: number;
    thumbnailFullWidth?: boolean;
    ageGate?: boolean;
    lazyLoad?: boolean;
}
